import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { Dropdown } from "primereact/dropdown";
import { InputSwitch } from "primereact/inputswitch";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { Message } from "primereact/message";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { accountActions } from "../../store/account/accountSlice";
import { userActions } from "../../store/user/userSlice";
import { IAppState } from "../../models/appState";
import { IAccount } from "../../models/account";
import { ILabelValue } from "../../models/labelValue";
import { v4 as uuidv4 } from "uuid";

const AccountEdit: React.FC = () => {
  const industryData = [
    "Apparel",
    "Banking",
    "Biotechnology",
    "Chemicals",
    "Communications",
    "Construction",
    "Consulting",
    "Education",
    "Electronics",
    "Energy",
    "Engineering",
    "Entertainment",
    "Environmental",
    "Finance",
    "Government",
    "Healthcare",
    "Hospitality",
    "Insurance",
    "Machinery",
    "Manufacturing",
    "Media",
    "Not For Profit",
    "Recreation",
    "Retail",
    "Shipping",
    "Technology",
    "Telecommunications",
    "Transportation",
    "Utilities",
    "Other",
  ];
  const typeData = [
    "Analyst",
    "Competitor",
    "Customer",
    "Integrator",
    "Investor",
    "Partner",
    "Press",
    "Prospect",
    "Reseller",
    "Other",
  ];

  const defaultItem: IAccount = {
    id: "",
    name: "",
    description: "",
    assignedUserId: "",
    assignedUserLastName: "",
    accountType: "",
    industry: "",
    annualRevenue: "",
    phoneFax: "",
    billingAddressStreet: "",
    billingAddressCity: "",
    billingAddressState: "",
    billingAddressPostalcode: "",
    billingAddressCountry: "",
    rating: "",
    phoneOffice: "",
    phoneAlternate: "",
    website: "",
    ownership: "",
    employees: "",
    tickerSymbol: "",
    shippingAddressStreet: "",
    shippingAddressCity: "",
    shippingAddressState: "",
    shippingAddressPostalcode: "",
    shippingAddressCountry: "",
    parentId: "",
    sicCode: "",
    email1: "",
    email2: "",
    emailOptout: false,
  };
  const navigateTo = "/accounts";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [dirty, setDirty] = useState(false); // if form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const params = useParams();
  const memo = useRef(true);
  const accountsData = useRef([] as ILabelValue[]);

  const [account, isFetching, isError, accounts, users] = useAppSelector(
    (state: IAppState) => [
      state.accounts.account,
      state.accounts.isFetching,
      state.accounts.isError,
      state.accounts.accounts,
      state.users.users,
    ],
    shallowEqual
  );
  const dispatch = useAppDispatch();

  const getMembers = useCallback(
    (id = ""): ILabelValue[] => {
      if (id) {
        return accounts
          .filter((x) => x.id !== id)
          .map((x) => {
            return { label: x.name, value: x.id };
          });
      }

      return accounts.map((x) => {
        return { label: x.name, value: x.id };
      });
    },
    [accounts]
  );

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      dispatch(userActions.getUsers());
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(accountActions.getAccountById(id));
          accountsData.current = getMembers(id);
        }
      } else {
        accountsData.current = getMembers();
      }
    }
  }, [params, dispatch, getMembers]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== account?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(account);
    }
  }, [params, item, account, isFetching, isError]);

  const isNameValid = useCallback(
    (name: string) => {
      let result = true;
      if (item.id.length === 0) {
        if (
          accounts.find((x) => x.name?.toLowerCase() === name?.toLowerCase())
        ) {
          result = false;
        }
      } else {
        if (
          accounts.find(
            (x) =>
              x.name?.toLowerCase() === name?.toLowerCase() && x.id !== item.id
          )
        ) {
          result = false;
        }
      }

      return result;
    },
    [item.id, accounts]
  );

  useEffect(() => {
    if (!dirty) {
      return;
    }

    setValid(
      (item.name || "").trim().length > 0 && isNameValid(item.name || "")
    );
  }, [dirty, item, isNameValid]);

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    if (tname === "assignedUserId") {
      const user = users.find((value) => value.id === tvalue);
      if (user) {
        setItem({
          ...item,
          [tname]: tvalue,
          assignedUserLastName: user.lastName,
        });
      }
    } else {
      setItem({ ...item, [tname]: tvalue });
    }

    if (tname === "name") {
      const isValidName = isNameValid(tvalue);
      setErrors({
        ...errors,
        nameInvalid: !isValidName,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    }
  };

  const onCopyBillingClick = () => {
    setDirty(true);
    setItem({
      ...item,
      shippingAddressStreet: item.billingAddressStreet,
      shippingAddressCity: item.billingAddressCity,
      shippingAddressState: item.billingAddressState,
      shippingAddressPostalcode: item.billingAddressPostalcode,
      shippingAddressCountry: item.billingAddressCountry,
    });
  };

  const onCopyShippingClick = () => {
    setDirty(true);
    setItem({
      ...item,
      billingAddressStreet: item.shippingAddressStreet,
      billingAddressCity: item.shippingAddressCity,
      billingAddressState: item.shippingAddressState,
      billingAddressPostalcode: item.shippingAddressPostalcode,
      billingAddressCountry: item.shippingAddressCountry,
    });
  };

  const onSaveClick = async () => {
    if (subtitle !== "Edit") {
      item.id = uuidv4();
    }

    dispatch(accountActions.updateAccount(item));
    setTimeout(() => {
      dispatch(accountActions.getAccounts());
    }, 300);
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  return (
    <>
      <h3 className="caption">Account: {subtitle}</h3>
      <div className="grid">
        <div className="col-12">
          <span className="field-required">*</span> - Required field
        </div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Account</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="AccountName">
                  Account Name <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="AccountName"
                  name="name"
                  maxLength={150}
                  placeholder="Microsoft"
                  value={item.name}
                  onChange={onChange}
                />
                {(errors as any)?.name && (
                  <Message severity="error" text="Account Name is required." />
                )}
                {(errors as any)?.nameInvalid && (
                  <Message
                    severity="error"
                    text="Account Name already exists."
                  />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="PhoneOffice">Office Phone</label>
              </div>
              <div className="col">
                <InputText
                  id="PhoneOffice"
                  name="phoneOffice"
                  maxLength={25}
                  placeholder="234-567-8901"
                  value={item.phoneOffice}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="PhoneFax">Fax</label>
              </div>
              <div className="col">
                <InputText
                  id="PhoneFax"
                  name="phoneFax"
                  maxLength={25}
                  placeholder="234-567-8901"
                  value={item.phoneFax}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="PhoneOther">Other&nbsp;Phone</label>
              </div>
              <div className="col">
                <InputText
                  id="PhoneOther"
                  name="phoneAlternate"
                  maxLength={25}
                  placeholder="234-567-8901"
                  value={item.phoneAlternate}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Email1">Primary Email</label>
              </div>
              <div className="col">
                <InputText
                  id="Email1"
                  name="email1"
                  maxLength={255}
                  placeholder="Example jdoe@example.com"
                  value={item.email1}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Email2">Secondary Email</label>
              </div>
              <div className="col">
                <InputText
                  id="Email2"
                  name="email2"
                  maxLength={255}
                  placeholder="Example jdoe@example.com"
                  value={item.email2}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Website">Website</label>
              </div>
              <div className="col">
                <InputText
                  id="Website"
                  name="website"
                  maxLength={255}
                  value={item.website}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="TickerSymbol">Ticker Symbol</label>
              </div>
              <div className="col">
                <InputText
                  id="TickerSymbol"
                  name="tickerSymbol"
                  maxLength={10}
                  value={item.tickerSymbol}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="MemberOf">Member Of</label>
              </div>
              <div className="col">
                <Dropdown
                  id="MemberOf"
                  name="parentId"
                  options={accountsData.current}
                  value={item.parentId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Employees">Employees</label>
              </div>
              <div className="col">
                <InputText
                  id="Employees"
                  name="employees"
                  maxLength={10}
                  value={item.employees}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Ownership">Ownership</label>
              </div>
              <div className="col">
                <InputText
                  id="Ownership"
                  name="ownership"
                  maxLength={100}
                  value={item.ownership}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Rating">Rating</label>
              </div>
              <div className="col">
                <InputText
                  id="Rating"
                  name="rating"
                  maxLength={25}
                  value={item.rating}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Industry">Industry</label>
              </div>
              <div className="col">
                <Dropdown
                  id="Industry"
                  name="industry"
                  options={industryData}
                  value={item.industry}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="SICCode">SIC Code</label>
              </div>
              <div className="col">
                <InputText
                  id="SICCode"
                  name="sicCode"
                  maxLength={10}
                  value={item.sicCode}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Type">Type</label>
              </div>
              <div className="col">
                <Dropdown
                  id="Type"
                  name="accountType"
                  options={typeData}
                  value={item.accountType}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="AnnualRevenue">Annual Revenue</label>
              </div>
              <div className="col">
                <InputText
                  id="AnnualRevenue"
                  name="annualRevenue"
                  maxLength={25}
                  value={item.annualRevenue}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="AssignedTo">Assigned To</label>
              </div>
              <div className="col">
                <Dropdown
                  id="AssignedTo"
                  name="assignedUserId"
                  options={users}
                  optionLabel="lastName"
                  optionValue="id"
                  value={item.assignedUserId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="EmailOptOut">Email Opt Out</label>
              </div>
              <div className="col">
                <div className="p-fixed-switch">
                  <InputSwitch
                    id="EmailOptOut"
                    name="emailOptout"
                    checked={item.emailOptout || false}
                    value="0"
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Address</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col">
            <h4>Billing Address</h4>
          </div>
          <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
          <div className="col hidden-xs">
            <h4>Shipping Address</h4>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="BillingStreet">Street</label>
              </div>
              <div className="col">
                <InputText
                  id="BillingStreet"
                  name="billingAddressStreet"
                  maxLength={150}
                  value={item.billingAddressStreet}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">
                <label htmlFor="ShippingStreet1">Street</label>
              </div>
              <div className="col hidden-xs">
                <InputText
                  id="ShippingStreet1"
                  name="shippingAddressStreet"
                  maxLength={150}
                  value={item.shippingAddressStreet}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="BillingCity">City</label>
              </div>
              <div className="col">
                <InputText
                  id="BillingCity"
                  name="billingAddressCity"
                  maxLength={100}
                  value={item.billingAddressCity}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed-10 hidden-xs">
                <Button
                  icon="pi pi-arrow-circle-right"
                  className="p-button-rounded p-button-text p-button-lg"
                  tooltip="Copy Billing Address"
                  onClick={onCopyBillingClick}
                />
              </div>
              <div className="col hidden-xs">
                <label htmlFor="ShippingCity1">City</label>
              </div>
              <div className="col hidden-xs">
                <InputText
                  id="ShippingCity1"
                  name="shippingAddressCity"
                  maxLength={100}
                  value={item.shippingAddressCity}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col">
            <div className="grid">
              <div className="col">
                <label htmlFor="BillingState">State</label>
              </div>
              <div className="col">
                <InputText
                  id="BillingState"
                  name="billingAddressState"
                  maxLength={100}
                  value={item.billingAddressState}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">
                <label htmlFor="ShippingState1">State</label>
              </div>
              <div className="col hidden-xs">
                <InputText
                  id="ShippingState1"
                  name="shippingAddressState"
                  maxLength={100}
                  value={item.shippingAddressState}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="BillingPostalCode">Postal Code</label>
              </div>
              <div className="col">
                <InputText
                  id="BillingPostalCode"
                  name="billingAddressPostalcode"
                  maxLength={20}
                  value={item.billingAddressPostalcode}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed-10 hidden-xs">
                <Button
                  icon="pi pi-arrow-circle-left"
                  className="p-button-rounded p-button-text p-button-lg"
                  tooltip="Copy Shipping Address"
                  onClick={onCopyShippingClick}
                />
              </div>
              <div className="col hidden-xs">
                <label htmlFor="ShippingPostalCode1">Postal Code</label>
              </div>
              <div className="col hidden-xs">
                <InputText
                  id="ShippingPostalCode1"
                  name="shippingAddressPostalcode"
                  type="text"
                  maxLength={20}
                  value={item.shippingAddressPostalcode}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="BillingCountry">Country</label>
              </div>
              <div className="col">
                <InputText
                  id="BillingCountry"
                  name="billingAddressCountry"
                  maxLength={255}
                  value={item.billingAddressCountry}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">
                <label htmlFor="ShippingCountry1">Country</label>
              </div>
              <div className="col hidden-xs">
                <InputText
                  id="ShippingCountry1"
                  name="shippingAddressCountry"
                  maxLength={255}
                  value={item.shippingAddressCountry}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col">
                <Button
                  icon="pi pi-arrow-circle-down"
                  className="p-button-rounded p-button-text p-button-lg"
                  tooltip="Copy Billing Address"
                  onClick={onCopyBillingClick}
                />
              </div>
              <div className="col">
                <Button
                  icon="pi pi-arrow-circle-up"
                  className="p-button-rounded p-button-text p-button-lg"
                  tooltip="Copy Shipping Address"
                  onClick={onCopyShippingClick}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <h4>Shipping Address</h4>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col-12">
                <label htmlFor="ShippingStreet2">Street</label>
              </div>
              <div className="col-12">
                <InputText
                  id="ShippingStreet2"
                  name="shippingAddressStreet"
                  maxLength={150}
                  value={item.shippingAddressStreet}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col-12">
                <label htmlFor="ShippingCity2">City</label>
              </div>
              <div className="col-12">
                <InputText
                  id="ShippingCity2"
                  name="shippingAddressCity"
                  maxLength={100}
                  value={item.shippingAddressCity}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col-12">
                <label htmlFor="ShippingState2">State</label>
              </div>
              <div className="col-12">
                <InputText
                  id="ShippingState2"
                  name="shippingAddressState"
                  maxLength={100}
                  value={item.shippingAddressState}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col-12">
                <label htmlFor="ShippingPostalCode2">Postal Code</label>
              </div>
              <div className="col-12">
                <InputText
                  id="ShippingPostalCode2"
                  name="shippingAddressPostalcode"
                  maxLength={20}
                  value={item.shippingAddressPostalcode}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col-12">
                <label htmlFor="ShippingCountry2">Country</label>
              </div>
              <div className="col-12">
                <InputText
                  id="ShippingCountry2"
                  name="shippingAddressCountry"
                  maxLength={255}
                  value={item.shippingAddressCountry}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Description</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <InputTextarea
              name="description"
              value={item.description}
              onChange={onChange}
              autoResize
            />
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
        <div className="col-12">
          <Button
            icon="pi pi-check"
            className="p-button-rounded p-button-outlined"
            tooltip="Save"
            disabled={!valid}
            onClick={onSaveClick}
          />
          &nbsp; &nbsp;
          <Button
            icon="pi pi-times"
            className="p-button-rounded p-button-outlined"
            tooltip="Back to List"
            onClick={onCancelClick}
          />
        </div>
      </div>
    </>
  );
};

export default AccountEdit;
